import { motion } from 'framer-motion';

const slideIn = {
    initial: {
        x: '100vh',
        opacity: 0,
    },
    animate: {
        x: 0,
        opacity: 1,
        transition: {
            duration: 0.1,
            type: 'spring',
            damping: 25,
            stiffness: 500,
        }
    },
    exit: {
        x: '-25vh',
        opacity: 0,
    },
};

const Notification = props => {
    return (
        <motion.div 
            className={`notification ${props.type}`}
            variants={slideIn}
            initial='initial'
            animate='animate'
            exit='exit'
        >
            {props.icon}{props.message}
        </motion.div>
    )
}

export default Notification;